import React from "react"
import BaseLayout from "../components/static/BaseLayout"

import 'tui-image-editor/dist/tui-image-editor.css';
import ImageEditor from '@toast-ui/react-image-editor';

const myTheme = {
  // Theme object to extends default dark theme.
};

const DataProtectionPage = () => {

  React.useEffect(() => {

    if(window && (typeof window !== "undefined") && (window as any).tui){
      console.log("RUN");
      const options = {
        //...
        usageStatistics: false,
      };

      const imageEditor = new (window as any).tui.ImageEditor('#tui-image-editor-container', options);

    }

  }, []);

  return (
    <BaseLayout>
      <h1>Data protection</h1>
      
      {window && (typeof window !== "undefined") ? <ImageEditor
    includeUI={{
      loadImage: {
        path: '/images/rein_eu.png',
        name: 'SampleImage',
      },
      theme: myTheme,
      menu: ['shape', 'filter'],
      initMenu: 'filter',
      uiSize: {
        width: '1000px',
        height: '700px',
      },
      menuBarPosition: 'bottom',
    }}
    cssMaxHeight={500}
    cssMaxWidth={700}
    selectionStyle={{
      cornerSize: 20,
      rotatingPointOffset: 70,
    }}
    usageStatistics={true}
  /> : null}
      
      
    </BaseLayout>
  )
}

export default DataProtectionPage
